import BootPay from 'bootpay-js';

export const bootPayRequest = form => {
  const { book, user, bookId, orderId, buyMyBook, studentId } = form;
  BootPay.request({
    price: book.price, //  
    application_id: '5edb7b5c8f0751002bfcd4bc',
    name: book.name, //  
    pg: 'inicis',
    method: '', //,      .
    show_agree_window: 1, //      
    items: [
      {
        item_name: book.name, //
        qty: 1, //
        unique: bookId, //   primary key
        price: book.price, // 
      },
    ],
    user_info: {
      username: user.name,
      email: user.email,
      phone: user.phone,
    },
    order_id: orderId, // ,    .
    account_expire_at: '2018-05-25', //    ( yyyy-mm-dd  .  . )
    extra: {
      vbank_result: 1, //   ,   (1), (0),  (1)
      quota: '0,2,3', //  5      , [0(), 2, 3] ,  12 
    },
  })
    .error(function(data) {
      //    .
      console.log(data);
    })
    .cancel(function(data) {
      //  .
      console.log(data);
    })
    .ready(function(data) {
      //      .
      console.log(data);
    })
    .confirm(function(data) {
      //   ,     .
      // -       .
      console.log(data);
      var enable = true; //       
      if (enable) {
        BootPay.transactionConfirm(data); //     .
      } else {
        BootPay.removePaymentWindow(); //         .
      }
    })
    .close(function(data) {
      //   . (,,   )
      console.log(data);
    })
    .done(function(data) {
      //   
      //        .
      let formValue = {
        bookId: bookId,
        receiptId: data.receipt_id,
        studentId: studentId,
      };
      buyMyBook(formValue, () => console.log('complete'));
      console.log(data);
      
    });
};
