import React, { Component } from 'react';
import { Button, Header, Image, Modal } from 'semantic-ui-react';

export default class AutoContainModal extends Component {
  state = { open: false };

  show = dimmer => () => this.setState({ dimmer, open: true });
  close = () => this.setState({ open: false });
  autoBuy = () => {
    const { buyMyBook, id } = this.props;
    const studentId = window.sessionStorage.getItem('studentId');
    const formValue = {
      studentId: studentId,
      bookId: id,
    };
    buyMyBook(formValue, () => this.close());
  };

  render() {
    const { open, dimmer } = this.state;
    return (
      <div>
        <Button onClick={this.show(true)}></Button>
        <Modal dimmer={dimmer} open={open} onClose={this.close}>
          <Modal.Content image>
            <Image wrapped size="medium" src={this.props.cover} />
            <Modal.Description>
              <Header>
                    .   ?
              </Header>
            </Modal.Description>
          </Modal.Content>
          <Modal.Actions>
            <Button color="black" onClick={this.close}>
              
            </Button>
            <Button
              positive
              icon="checkmark"
              labelPosition="right"
              content="  "
              onClick={this.autoBuy}
            />
          </Modal.Actions>
        </Modal>
      </div>
    );
  }
}
