export function validate(values, isCheck, isOnlyId) {
  const { name, userId, password, grade, birthday, phoneNumber } = values;
  let errors = {};
  if (!name) errors['name'] = '  ';
  if (!userId) errors['userId'] = '  ';
  if (userId && !isCheck) errors['userId'] = '   ';
  if (userId && isCheck && !isOnlyId) errors['userId'] = '   ';
  if (!password) errors['password'] = '   ';
  if (!grade) errors['grade'] = '  ';
  if (grade && (grade >= 4 || grade < 1)) errors['grade'] = '  1~3 ';
  if (!birthday) errors['birthday'] = ' ';
  if (birthday && birthday.length !== 6)
    errors['birthday'] = '6  . ex)001205';
  if (!phoneNumber) errors['phoneNumber'] = '  ';
  if (phoneNumber && phoneNumber.length !== 11)
    errors['phoneNumber'] = '  ex)01012341234';

  return errors;
}
