import React from 'react';
import { Grid, Label, Segment } from 'semantic-ui-react';

export default function BookInfo(props) {
  const { book } = props;
  return (
    <Grid columns={1} rows={2}>
      <Grid.Column>
        <Segment raised>
          <Label as="a" color="red" ribbon>
            Overview
          </Label>
          <br />
          <span>
            : {book.name} ({book.grade}) <br />
            : {book.subject} <br />
            : {book.price} <br />
            : {book.publishedDate}
          </span>
          <br />
          <Label as="a" color="orange" ribbon>
            Introduction
          </Label>
          <br />
          <span>{book.information ? book.information : '  .'}</span>
        </Segment>
      </Grid.Column>
    </Grid>
  );
}
